<template>
  <div class="am-setUp">
    <!-- <SignUp></SignUp> -->
    <van-button type="primary" size="large" @click="out">退出登录</van-button>
  </div>
</template>

<script>
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"

  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  const navBar_Every=createNamespacedHelpers('navBar_Every')
  export default{
    name:"",
    /* 数据*/
    data(){
      return {

      }
    },
    /* 方法*/
    methods:{
      // 导航栏标题
      // ...navBar_Every.mapMutations({
      //       	titleHandler: 'titleHandler'
      //       }),
      ...login.mapMutations({
              hasLoginHandler: 'hasLoginHandler'
            }),
      ...login.mapMutations({
              delUserInfoHandler: 'delUserInfoHandler'
            }),
      // 退出登录
      out(){
        /* 移除token*/
        localStorage.removeItem('token')
        /* 中断socket连接*/
        this.$socket.disconnect()
        /* 删除用户信息*/
        this.delUserInfoHandler()
        /* 将用户登录状态为false*/
        this.hasLoginHandler(false)
        this.$toast("退出账号成功")
        this.$router.back();
        this.$router.replace({
          path:'/login'
        })
      },
    },
    /* 计算*/
    computed:{

    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
    },
    /* 挂载阶段*/
    mounted() {

      // this.titleHandler('设置')
    },
    activated(){
      // this.oneHandler(['myCol','myColMore'])

    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
  .am-setUp{
    @extend %top;
  }
</style>



